<script setup>
    import { Suspense } from 'vue'
    import { collectOptions } from '@/helpers/context.js'
    import CompanyTaskMentionComponent from '@/components/company/task_mentions/CompanyTaskMentionComponent.vue'

    const options = collectOptions()
    const redirectOnsave = () => {
        window.location.replace('/companies/' + options.company_id)
    }

    const redirectOnCancel = () => {
        if (options['come_from']) {
            window.location.replace(options['come_from'])
        } else {
            window.location.href = options.context_url.replace('/api/v1/', '/')
        }
    }
</script>

<template>
    <Suspense>
        <div>
            <CompanyTaskMentionComponent
                :url="options.context_url"
                :form-config-url="options.form_config_url"
                @on-save="redirectOnsave"
                @on-cancel="redirectOnCancel"
            />
        </div>
        <template #fallback> Chargement... </template>
    </Suspense>
</template>
