<script setup>
    import { ref } from 'vue'
    import { storeToRefs } from 'pinia'

    import FormFlatLayout from '@/layouts/FormFlatLayout.vue'
    import CompanyTaskMentionForm from './CompanyTaskMentionForm.vue'
    import {
        useConfigurableOptionConfigStore,
        useConfigurableOptionStore,
    } from '@/stores/configurable_option'

    // props attendu par le composant
    const props = defineProps({
        optionId: { type: Number || null, default: null },
        layout: { type: Object, default: FormFlatLayout },
    })

    const emit = defineEmits(['saved', 'cancel'])

    const isEdit = !!props.optionId
    const loading = ref(true)

    const dataStore = useConfigurableOptionStore()
    const configStore = useConfigurableOptionConfigStore()

    const preload = async () => {
        const promises = [configStore.loadConfig()]
        if (isEdit) {
            promises.push(dataStore.loadOption(props.optionId))
        } else {
            promises.push(dataStore.setOptionId(null))
        }
        Promise.all(promises).then(() => (loading.value = false))
    }

    const { currentOption } = storeToRefs(dataStore)

    function onSaved(option) {
        emit('saved', option)
    }

    function onCancel() {
        console.log('Cancel Option add/edit')
        emit('cancel')
    }

    await preload()
</script>

<template>
    <div v-if="loading">Chargement des informations</div>
    <CompanyTaskMentionForm
        v-else
        :option="currentOption"
        :layout="layout"
        @saved="onSaved"
        @cancel="onCancel"
    />
</template>
