<script setup>
    import { ref } from 'vue'
    import {
        useConfigurableOptionConfigStore,
        useConfigurableOptionStore,
    } from '@/stores/configurable_option'
    import { storeToRefs } from 'pinia'
    import CompanyTaskMentionFormComponent from './CompanyTaskMentionFormComponent.vue'
    import FormModalLayout from '@/layouts/FormModalLayout.vue'
    import Table from '@/components/lists/Table.vue'
    import columnsDef from './columnDefs'
    import Button from '@/components/Button.vue'
    import Icon from '@/components/Icon.vue'

    /**
     *
     * Charge les éléments
     * Charge le schéma de données
     *
     *
     * 1- Affichage d'une Liste
     * 2- Formulaire d'ajout / Modification d'un élément
     */

    const props = defineProps({
        url: { type: String, required: true },
        formConfigUrl: { type: String, required: true },
    })
    const showForm = ref(false)
    const currentOptionId = ref(null)
    const listParams = ref({
        columnsDef: columnsDef,
        columns: columnsDef.map((column) => column.name),
    })

    const dataStore = useConfigurableOptionStore()
    const configStore = useConfigurableOptionConfigStore()
    configStore.setUrl(props.formConfigUrl)

    const { optionCollection } = storeToRefs(dataStore)
    async function fetchData() {
        const promise = dataStore.setInitialValues(props.url)
        const promise2 = configStore.loadConfig()
        await Promise.all([promise, promise2])
    }
    const addOption = () => {
        showForm.value = true
    }
    const editOption = (optionId) => {
        showForm.value = true
        currentOptionId.value = optionId
    }
    const onCancelAddEdit = () => {
        showForm.value = false
        currentOptionId.value = null
    }
    const onSaved = (option) => {
        console.log('Save option', option)
        showForm.value = false
        currentOptionId.value = null
    }
    await fetchData()
</script>

<template>
    <div class="limited_width width60">
        <div class="alert alert-info">
            Vous pouvez configurer ici des mentions spécifiques qui vous seront
            proposées lorsque vous éditez des devis ou des factures.<br />
            Celles-ci apparaîtront alors dans les fichiers PDF sous la forme de
            cadres en dessous des prestations.
        </div>
        <h2>Liste des mentions</h2>
        <Table :items="optionCollection" :params="listParams">
            <template #rowActions="{ item }">
                <td class="col_actions width_two">
                    <div class="btn-group">
                        <Button
                            :show-label="false"
                            icon="pen"
                            label="Modifier"
                            title="Ouvrir le formulaire de modification de l'élément"
                            @click="() => editOption(item.id)"
                        />
                        <Button
                            :show-label="false"
                            icon="trash-alt"
                            css="negative"
                            label="Supprimer"
                            title="Supprimer l'élément"
                            @click="() => dataStore.deleteOption(item.id)"
                        />
                    </div>
                </td>
            </template>
        </Table>

        <Button
            icon="plus"
            label="Ajouter un élément"
            @click="addOption"
            :show-label="true"
        ></Button>
        <!-- Formulaire d'ajout / Modification d'un élément -->
        <CompanyTaskMentionFormComponent
            v-if="showForm"
            :option-id="currentOptionId"
            :layout="FormModalLayout"
            @cancel="onCancelAddEdit"
            @saved="onSaved"
        />
    </div>
</template>
