<script setup>
    import { ref, computed } from 'vue'
    import { useForm } from 'vee-validate'
    import {
        buildYupSchema,
        getFieldData,
        getDefaults,
        getSubmitErrorCallback,
        getSubmitModelCallback,
    } from '@/helpers/form'
    import Input from '@/components/forms/Input.vue'
    import FormFlatLayout from '@/layouts/FormFlatLayout.vue'
    import {
        useConfigurableOptionConfigStore,
        useConfigurableOptionStore,
    } from '@/stores/configurable_option'
    import RichTextArea from '@/components/forms/RichTextArea.vue'

    const props = defineProps({
        isEdit: { type: Boolean, default: false },
        option: { type: Object, default: null },
        layout: { type: Object, default: FormFlatLayout },
    })

    const emit = defineEmits(['saved', 'cancel', 'error'])

    const dataStore = useConfigurableOptionStore()
    const configStore = useConfigurableOptionConfigStore()

    // Configuration du schéma de formulaire et du Form vee-validate
    const jsonSchema = configStore.getSchema('default')
    const formSchema = buildYupSchema(jsonSchema)
    const initialValues = getDefaults(formSchema)

    Object.assign(initialValues, props.option)

    const { values, handleSubmit, isSubmitting } = useForm({
        validationSchema: formSchema,
        initialValues: initialValues,
    })

    /**
     * Fonction lancée lorsque la validation des données par vee-validate
     * est réussie/en erreur
     */
    const onSubmitSuccess = getSubmitModelCallback(emit, dataStore.save)
    const onSubmitError = getSubmitErrorCallback(emit)

    const onSubmit = handleSubmit(onSubmitSuccess, onSubmitError)
    const onCancel = () => emit('cancel')

    const getData = (fieldName) => getFieldData(formSchema, fieldName)
</script>

<template>
    <component :is="layout" @submit-form="onSubmit" @close="onCancel">
        <template #title>
            <template v-if="isEdit"> Modifier une mention </template>
            <template v-else> Ajouter une mention </template>
        </template>
        <template #fields>
            <fieldset class="deformFormFieldset">
                <Input v-bind="getData('label')" />
                <Input v-bind="getData('help_text')" />
            </fieldset>
            <fieldset class="deformFormFieldset">
                <legend>Sortie PDF</legend>
                <Input v-bind="getData('title')" />
                <RichTextArea v-bind="getData('full_text')" />
            </fieldset>
        </template>
        <template #buttons>
            <div class="btn-group">
                <button
                    id="deformsubmit"
                    name="submit"
                    type="submit"
                    class="btn btn-primary"
                    value="submit"
                    :disabled="isSubmitting"
                >
                    {{ isEdit ? 'Modifier' : 'Valider' }}
                </button>
                <button
                    id="deformcancel"
                    name="cancel"
                    type="button"
                    class="btn btn-default"
                    @click="() => emit('cancel')"
                >
                    Annuler
                </button>
            </div>
        </template>
    </component>
</template>
